.sendForm {
	margin-top: 30px;
	width: 100%;
	height: 40px;
	display: flex;
}

.textarea {
	background-color: #F5F7FB;
	border: 1px solid #D9D9D9;
	width: 70%;
	min-height: 40px;
	border-radius: 20px;
	resize: none;
	padding: 10px;
	margin-right: 40px;
}

.button {
	width: 120px;
	height: 40px;
	border-radius: 20px;
	background-color: #0066CC;
	border: 1px solid #0066CC;
}

