table {
	table-layout: fixed;
	width: 600px;
	border-collapse: collapse;
	border: 2px solid white;
 }
 thead th:nth-child(1) {
	width: 30%;
	background-color: aqua;
 }
 
 thead th:nth-child(2) {
	width: 20%;
 }
 
 thead th:nth-child(3) {
	width: 15%;
 }
 
 thead th:nth-child(4) {
	width: 35%;
 }
 
 th,
 td {
	padding: 20px;
	border: 1px solid maroon;
 }
 th:last-of-type {
	border: 2px solid white;
}
 th {
	letter-spacing: 2px;
	
 }
 
 td {
	letter-spacing: 1px;
	
 }

 tbody td {
	text-align: center;
	
 }
 
 tfoot th {
	text-align: right;
	
 }
 thead th,
tfoot th {
  font-family: "Rock Salt", cursive;
}
 