.friendMessage {
	display: flex;
	justify-content: flex-start;
	position: relative;
}

.friendImageAndText {
	display: flex;
}

.friendImageAndText img {
	width: 48px;
	height: 48px;
	margin: 8px;
}

.friendText {
	display: flex;
	flex-direction: column;
	min-height: 60px;
	width: content;
	background-color: #F5F7FB;
	color: #FFFFFF;
	position:relative;
	padding:10px 10px;
	border-radius:10px;
	float: left;
}

.friendText:after {
	content: "";
	position: absolute;
	z-index: 3;
	left: -2%;
	bottom: 2%;
	border: 18px solid transparent;
	border-bottom: 8px solid #F5F7FB;
}

.friendName {
	font-family: Montserrat;
	font-size: 16px;
	font-weight: 600;
	line-height: 20px;
	letter-spacing: 0em;
	text-align: left;	
	color: #000000;
}

.friendMessageText {
	font-family: Montserrat;
	font-size: 16px;
	font-weight: 400;
	line-height: 20px;
	letter-spacing: 0em;
	color: #000000;
}

.friendTime {
	position: absolute;
	right: 97%;
	top: 100%;
	font-family: Montserrat;
	font-size: 10px;
	font-weight: 600;
	line-height: 12px;
	letter-spacing: 0em;
}

